import Node from "./Node";
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { el, parent, wishlist, item, subitem, done, priority, dbObject } from './TypesInterfaces'

export type data = {
    name: string;
    done: done;
    cost: number;
    priority: priority;
    due_date: string;
    user?: string;
}

export class AppNode extends Node {
    data: data;
    children: AppNode[] = [];

    constructor(id: string, data: data) {
        super(id, data)
        this.data = data;
    }

    get name(): string {
        return this.data?.name as string
    }
    get due_date(): string {
        return this.data?.due_date as string
    }
    get priority(): priority {
        return this.data?.priority as priority
    }

    get cost(): number {
        if (this.isLeaf)
            return this.data.cost as number;
        else
            return this.total_cost
    }

    get done(): done {
        if (this.isLeaf)
            return this.data.done as done;
        else
            return this.children.every(child => child.done === 100) ? 100 : 0;
    }
    set done(done: done) {
        this.data.done = done;
    }

    // computed
    get total_cost(): number {
        return this.children.reduce((prev, cur) => prev + cur.cost, 0)
    }
    get total_spent(): number {
        return this.children
            .reduce((prev, cur) => {
                // for leaf nodes check from the done status,
                // for parent nodes just sum the total spent values
                if (cur.isLeaf)
                    return cur.done === 100 ? prev + cur.cost : prev + 0;
                else return prev + cur.total_spent
            }, 0)
    }
    get remaining(): number {
        return this.total_cost - this.total_spent
    }

    get total_done(): number {
        return this.children.filter(child => child.data?.done === 100).length;
    }
    get total_undone(): number {
        return this.children.filter(child => child.data?.done === 0).length;
    }
    get progress(): number {
        if (this.total_cost)
            return Math.round((this.total_spent / this.total_cost) * 100)
        else return 0;
    }
}

