import { breadthFirst, depthFirstPreOrder, depthFirstPostOrder } from '../helpers/tree-algorithms.js'
import type { traversalNames } from './TypesInterfaces'

export default class Node {
    readonly id: string;
    data: Record<string, string | number | undefined> | undefined;
    parent: Node | undefined = undefined;
    children: Node[] = [];

    private readonly traversalMethods = {
        breadth: breadthFirst,
        pre: depthFirstPreOrder,
        post: depthFirstPostOrder,
    }

    constructor(id: string, data?: Record<string, string | number | undefined>) {
        this.id = id;
        this.data = data;
    }

    static addChildToNode(node: Node, child: Node): Node {
        child.parent = node;
        node.children.push(child);

        return node;
    }

    // static parse(id:string, data: object) {
    //     const node = new Node(id);

    //     if (data.children) {
    //         for (const child of data.children)
    //             Node.addChildToNode(node, Node.parse(child));
    //     }

    //     delete data.children;

    //     return node;
    // }

    // static getTreeAsObject(rootNode: Node) {
    //     let obj: data = {
    //         ...rootNode.data,
    //         children: []
    //     };

    //     if (rootNode.children) {
    //         for (const child of rootNode.children) {
    //             const node = this.getTreeAsObject(child);
    //             obj.children!.push(node);
    //         }
    //     }

    //     if (rootNode.isLeaf) delete obj.children;

    //     return obj;
    // }

    /* ========== Methods: Instance ========== */

    addChild(child: Node): Node {
        if (!(child instanceof Node))
            throw new TypeError("Child must be of type Node.");

        child.parent = this;
        this.children.push(child);

        return child;
    }

    drop(): Node {
        let indexOfChild;

        if (!this.isRoot) {
            indexOfChild = this.parent!.children.indexOf(this);

            this.parent!.children.splice(indexOfChild, 1);
            this.parent = undefined;

            delete this.parent;
        }

        return this;
    }

    sort(compareFn: (a: Node, b: Node) => number): void {
        this.children.sort(compareFn)
    }

    /* ========== Methods: Traversal ========== */

    walk(fun: (node: Node) => any, strategy: traversalNames = 'pre'): void {
        return this.traversalMethods[strategy].call(this, fun)
    }

    first(fun: (node: Node) => any, strategy: traversalNames = 'pre'): Node | undefined {
        let first: Node | undefined;

        const firstFun = (node: any) => {
            if (fun.call(undefined, node)) {
                first = node;
                return false;
            }
        }

        this.traversalMethods[strategy].call(this, firstFun);

        return first;
    }

    all(fun: (node: Node) => any, strategy: traversalNames = 'pre'): Node[] {
        const all: Node[] = [];

        const allFun = (node: any) => {
            if (fun.call(undefined, node))
                all.push(node)
        }

        this.traversalMethods[strategy].call(this, allFun);

        return all;
    }

    /* ========== Properties ========== */
    /* Setters */
    set index(index: number) {
        if (this.isRoot) {
            if (index === 0) return;
            throw new Error("Invalid index.");
        }

        if (index < 0 || index >= this.parent!.children.length)
            throw new Error("Invalid index.");

        const oldIndex = this.parent!.children.indexOf(this);

        this.parent!.children.splice(
            index,
            0,
            this.parent!.children.splice(oldIndex, 1)[0]
        );
    }

    /* Getters */
    get index(): number {
        if (this.isRoot) return 0;
        return this.parent!.children.indexOf(this);
    }

    get isRoot(): boolean {
        return this.parent === undefined;
    }

    get isLeaf(): boolean {
        return this.children.length === 0;
    }

    get getDepth(): number {
        let d = 0,
            temp = this.parent;
        while (temp != undefined) {
            temp = temp.parent;
            d++;
        }
        return d;
    }

    get getHeight(): number {
        const darry: number[] = [];
        this.walk((node: Node) => darry.push(node.getDepth));
        return Math.abs(this.getDepth - Math.max(...darry));
    }

    get getPath(): Node[] {
        const path = [];

        (function addToPath(node: Node) {
            path.unshift(node);
            if (node.parent !== undefined) addToPath(node.parent);
        })(this);

        return path;
    }
}