// Traverse a tree
export function breadthFirst(callback) {
  const queue = [];
  queue.push(this);
  (function processQueue() {
    let keepGoing, node;

    if (queue.length === 0) return;

    node = queue.shift();

    for (const child of node.children) queue.push(child);

    keepGoing = callback.call(undefined, node);

    if (keepGoing !== false) processQueue();
  })();
}

export function depthFirstPreOrder(callback) {
  let keepGoing = callback.call(undefined, this);

  for (const child of this.children) {
    if (keepGoing === false) return false;
    keepGoing = depthFirstPreOrder.call(child, callback);
  }

  return keepGoing;
}

export function depthFirstPostOrder(callback) {
  let keepGoing;

  for (const child of this.children) {
    keepGoing = depthFirstPostOrder.call(child, callback);
    if (keepGoing === false) return false;
  }

  keepGoing = callback.call(undefined, this);

  return keepGoing;
}
