import Vue from 'vue'
import Vuex from 'vuex'

import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/functions";

import { AppNode, data } from "@/plugins/AppNode";
import { priority, done } from "@/plugins/TypesInterfaces";

Vue.use(Vuex)

type stateT = {
  user: null | Record<string, unknown>;
  unsubscribeAuthObserver: null | (() => void);
}

const state: stateT = {
  user: null,
  unsubscribeAuthObserver: null,
}

export default new Vuex.Store({
  state: state,

  getters: {
    isUserSignedIn: state => state.user !== null,
  },

  mutations: {
    SET_AUTH_USER(state, user) {
      state.user = user;
    },
    SET_UNSUBSCRIBE_AUTH_OBSERVER(state, unsubscribe) {
      state.unsubscribeAuthObserver = unsubscribe;
    },
  },

  actions: {
    // users
    initAuthentication({ commit, state }) {
      return new Promise((resolve) => {
        // unsubscribe observer if already listening
        if (state.unsubscribeAuthObserver)
          state.unsubscribeAuthObserver();

        const unsubscribe = firebase.auth().onAuthStateChanged((user) => {
          console.log("ðŸ‘£ the user has changed");

          if (user) {
            commit("SET_AUTH_USER", user);
            resolve(user);
          } else {
            commit("SET_AUTH_USER", null);
            resolve(null);
          }
        });

        commit("SET_UNSUBSCRIBE_AUTH_OBSERVER", unsubscribe);
      });
    },
    registerUserWithEmailAndPassword(context, { email, password }) {
      return firebase
        .auth()
        .createUserWithEmailAndPassword(email, password)
        .catch((e) => {
          throw e;
        });
    },
    signInWithEmailAndPassword(context, { email, password }) {
      return firebase.auth().signInWithEmailAndPassword(email, password);
    },
    signOut() {
      return firebase.auth().signOut();
    },

    // wishlists
    async buildUserWishlistsTree({ dispatch }, { uid }) {
      const root = new AppNode("root", { name: "Root" } as data);

      const wishlists = await dispatch("fetchAllWishlist", { uid });

      // loop starte
      for (const wishlist of wishlists) {
        const wishlistNode = root.addChild(
          new AppNode(wishlist.id, {
            name: wishlist.name,
            priority: wishlist.priority as priority,
            due_date: wishlist.due_date,
            user: wishlist.user,
            done: 0,
            cost: 0,
          })
        );

        const wishlistItems = await dispatch("fetchItems", {
          wishlistId: wishlist.id,
        });

        for (const item of wishlistItems) {
          const itemNode = wishlistNode.addChild(
            new AppNode(item.id, {
              name: item.name,
              priority: item.priority as priority,
              due_date: wishlist.due_date,
              done: item.done as done,
              cost: item.cost,
            })
          );

          const itemItems = await dispatch("fetchItems", {
            wishlistId: wishlist.id,
            itemId: item.id,
          });

          for (const subitem of itemItems) {
            itemNode.addChild(
              new AppNode(subitem.id, {
                name: subitem.name,
                done: subitem.done as done,
                due_date: "",
                cost: subitem.cost,
                priority: item.priority as priority,
              })
            );
          }
        }
      }
      // end

      return root;
    },

    async fetchAllWishlist(ctx, { uid }) {
      const wishlists = await firebase
        .firestore()
        .collection("wishlist")
        .where("user", "==", uid)
        .get()
        .then((querySnapshot) => {
          const docs: any[] = [];
          querySnapshot.forEach((doc) => {
            docs.push({ id: doc.id, ...doc.data() });
          });
          return docs;
        })
        .catch((e) => {
          throw e;
        });

      return wishlists;
    },
    async fetchItems(ctx, { wishlistId, itemId }) {
      let path = `wishlist/${wishlistId}/items/`
      if (itemId) path = path + `${itemId}/items`

      const items = await firebase
        .firestore()
        .collection(path)
        .get()
        .then((querySnapshot) => {
          const docs: any[] = [];
          querySnapshot.forEach((doc) => {
            docs.push({ id: doc.id, ...doc.data() });
          });
          return docs;
        })
        .catch((e) => {
          throw e;
        });

      return items;
    },
    async updateItem(ctx, { path, itemId, data }) {
      firebase
        .firestore()
        .collection(path)
        .doc(itemId)
        .update(data)
        .catch(e => console.log(e))
    },

    async addItem(ctx, { path, itemId, data }) {
      firebase
        .firestore()
        .collection(path)
        .doc(itemId)
        .set(data)
        .catch(e => console.log(e))
    },
    async deleteItem(ctx, { path, itemId }) {
      const deleteFn = firebase.functions().httpsCallable('recursiveDelete');

      deleteFn({ path: path + '/' + itemId })
        .then(function (result) {
          console.log('Delete success: ' + JSON.stringify(result));
        })
        .catch(function (err) {
          console.log('Delete failed, see console,');
          console.warn(err);
        });
    }
  },

  modules: {}
})
